body {
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
}

.form-wrap {
  text-align: left;
  position: fixed;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-left: 85px;
  margin-right: 85px;
  padding-top: 60px;
  display: flex;
}

.photo-card > a {
  height: fit-content;
  transform-origin: center;
  line-height: 0;
  transition: transform 1s;
  display: block;
}

.photo-card {
  color: #302c5a;
  border-radius: 8px;
  font-size: 14px;
  box-shadow: 0 0 16px #302c5a;
}

.photo-card:hover {
  transform: scale(1.1);
}

input {
  height: 40px;
  width: 200px;
  border: none;
  border-radius: 5px;
  margin-right: 20px;
  padding-top: 5px;
  padding-bottom: 0;
  padding-left: 10px;
  font-size: 24px;
}

input:hover, input:focus {
  outline: none;
}

input::-ms-input-placeholder {
  opacity: .7;
  font-size: 32px;
}

input::placeholder {
  opacity: .7;
  font-size: 32px;
}

.search-form {
  height: 100%;
  width: 80px;
  height: 80px;
  box-sizing: border-box;
  color: #000;
  border-radius: 50px;
  outline: none;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  transition: all .5s;
  display: flex;
  overflow: hidden;
  box-shadow: 0 0 16px #302c5a;
}

.search-form:hover, .search-form:focus {
  width: 360px;
  background-color: #302c5a;
  border-radius: 10px;
}

.search-btn {
  height: 45px;
  background-color: #418ba6;
  border-width: 0;
  border-radius: 5px;
  outline: none;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 18px;
  font-weight: bold;
}

.search-btn:hover {
  cursor: pointer;
  background-color: #3cc0be;
}

.info {
  justify-content: space-between;
  padding: 0 15px;
  display: flex;
}

.info-item {
  text-align: center;
}

.hidden {
  display: none;
}

img {
  width: 380px;
  height: 240px;
  object-fit: cover;
  transition: all .5s;
}

#myBtn {
  z-index: 99;
  color: #302c5a;
  cursor: pointer;
  opacity: .7;
  border: none;
  border-radius: 50%;
  outline: none;
  padding: 15px;
  font-size: 36px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  box-shadow: 0 0 16px #302c5a;
}

#myBtn:hover {
  background-color: #302c5a;
}

#loading {
  width: 50px;
  height: 50px;
  border: 3px solid #302c5a;
  border-top-color: #fff;
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

.visible {
  display: block;
}

.captions-style {
  text-align: right;
}

